import BackTop from '~/components/BackTop'
import Pagination from '~/components/Pagination'
import axios from '~/plugins/axios'
export default {
    components: {
        BackTop,
        Pagination
    },
    async asyncData({ env, route, error }) {
        let { data: { data } } = await axios.get(
            '/blog/post?pageSize=10&status=1&sort=created'
        )
        let { data: { data: tags } } = await axios.get('/blog/tags')
        if (!data) {
            error()
        }
        return {
            page: data,
            tags
        }
    },
    data() {
        return {
            tag: '',
            pageNo: 1
        }
    },
    computed: {
        pageSize() {
            return 10
        },
        pageList() {
            return [{}]
        },
        filterPageList() {
            return []
        }
    },
    methods: {
        changePage(n) {
            this.pageNo = n
            this.fetch()
        },
        changeTag(t) {
            this.tag = t
            this.pageNo = 1
            this.fetch()
        },
        async fetch() {
            let { data: { data } } = await axios.get('/blog/post', {
                params: {
                    pageNo: this.pageNo,
                    pageSize: this.pageSize,
                    status: 1,
                    sort: 'created',
                    tag: this.tag
                }
            })
            this.page = data
        },
        openUrl(id) {
            const { href } = this.$router.resolve({
                path: `/blog/${id}`
            })
            window.open(href, '_blank')
        }
    }
}
